import React,{useEffect, useState} from "react";
import Routes from "./Routes";
import VerticalLinearStepper from "pages/VerticalLinearStepper";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";


function App() {
  const location = useLocation();
  const currentUrl = location.pathname;
  const [activeStep, setActiveStep] = useState(0);
  const project_type = useSelector(store => store.basicInformation.projectType);
   useEffect(() => {
    const frontendPaths = ['/frontend-select-model','/frontend-select-techstack','/frontend-select-template','/frontend-select-capabilities','/frontend-basic-details','/frontend-review'];
    const backendPaths = ['/backend-select-model','/backend-select-techstack','/backend-select-db','/backend-select-capabilities','/backend-basic-details','/backend-review'];
    const bothPaths = ['/both-select-model','/both-select-techstack','/both-select-template','/both-select-backend','/both-select-db','/both-select-capabilities','/both-basic-details','/both-review'];
  
      if(project_type === 'Both'){
         const index = bothPaths.indexOf(currentUrl);
         console.log(index)
         setActiveStep(index);
      }
      if(project_type === 'Front End'){
        const index = frontendPaths.indexOf(currentUrl);
        console.log(index)
        setActiveStep(index);
     }
     if(project_type === 'Back End'){
      const index = backendPaths.indexOf(currentUrl);
      console.log(index)
      setActiveStep(index);
     }
      
  }, [currentUrl,project_type])
  
  const validateUrl = (param) => {
    switch(param) {
      case '/our-home':
      case '/homemain':
      case '/':
      case '/generating-frontend-code':
      case '/modellibrary':
      case '/settings':
      case '/help-and-support':
      case '/whats-new':
        return (
        <div className="bg-gray_900 flex font-inter items-start justify-start mx-[auto] w-[100%] min-h-screen px-[14px]">
          <Routes />               
        </div>
        )
      default:
        return (
        <div className="bg-gray_900 flex font-inter items-start justify-start mx-[auto] w-[100%] min-h-screen px-[14px]">
          <div className="flex md:flex-col flex-row gap-[48px] items-center justify-between max-w-[1256px] mx-[auto] md:px-[20px] w-[100%]">
            <VerticalLinearStepper activeStep={activeStep} projectType={project_type}/> 
            <Routes />       
          </div>
        </div>
        );
    }
  }

  return (
    <>
      {validateUrl(currentUrl)}
    </>
  )
}

export default App;
