import { createSlice } from '@reduxjs/toolkit';
const initialState = {
  frontend_details: {
    modelType: "",
    feTeckstack:'',
    // feTeckstack:{},
    FeTemplate:[],
    FrontendAppTheme : {},
    capablities:[],
    basicDetail:{
      ApplicationName : null,
      Namespace : null,
      ApplicationDescription : null,
      Architecture : null,
      DatabaseName : null,
      Server : null,
      BuildTool : null,
      PackageManagement : null
    }
  }
}
const frontendSlice = createSlice({
  name: "frontendSlice",
  initialState,
  reducers: {
      SET_FRONTEND_MODEL_DETAIL: (state, action) => {
        state.frontend_details.modelType = action.payload;
      },
      SET_FRONTEND_FETECKSTACK: (state, action) => {
        state.frontend_details.feTeckstack = action.payload;
      },
      ADD_FRONTEND_FETEMPLATES: (state, action) => {
        state.frontend_details.FeTemplate = action.payload;
      },
      ADD_FRONTEND_CAPABLITIES: (state, action) => {
        state.frontend_details.capablities = action.payload;
      },
      SET_FRONTEND_BASIC_DETAIL: (state, action) => {
        state.frontend_details.basicDetail = action.payload;
      },
      SET_FRONTEND_APP_THEMENAME: (state,action) => {
        console.log(action.payload);
        state.frontend_details.FrontendAppTheme = action.payload;
      },
      CLEAR_ALL_FRONTENDAPP_DETAIL: () => {
        console.log(initialState);
        return initialState;
      } 
  }
});

export const {
  SET_FRONTEND_MODEL_DETAIL,
  SET_FRONTEND_FETECKSTACK,
  ADD_FRONTEND_FETEMPLATES,
  ADD_FRONTEND_CAPABLITIES,
  SET_FRONTEND_BASIC_DETAIL,
  CLEAR_ALL_FRONTENDAPP_DETAIL,
  SET_FRONTEND_APP_THEMENAME,
} = frontendSlice.actions;
export default frontendSlice.reducer;